<script type="module">
    import axios from "axios";
    import $ from "jquery";
    import Vue from "vue";
    import VueLazyLoad from "vue-lazyload";
    import CartoonHeader from "./CartoonHeader";

    Vue.use(VueLazyLoad, {
        loading: require("../../assets/images/bg.gif"),
        error: require("../../assets/images/bg.gif")
    });

    export default {components: {
            "v-header": CartoonHeader
        },
        inject: ['reload'], //Appreload
        data() {
            return {
                cartoonResultList: null
            };
        },
        methods: {
            parentReload() {
                this.reload();
            },
            clickFunc() {
                this.$emit("parentReload");
            }
        },
        created() {
            let classfyName = this.$route.params.classfyName;
            axios.get("/cartoonPages?prefixUrl=%2Flist%2Fhanguomanhua%2F&pageIndex=1&classfyName=" +classfyName )
                .then((res)=>{
                    console.dir(res)
                });
            let flag = true;
            let pageIndex = 1;
            let _this = this;

            $(window).scroll(function () {
                if ($(document).scrollTop() >= ($(document).height() - $(window).height()) * 0.9) {
                    if (flag) {
                        pageIndex++;
                        flag = false;
                        let categoryId = _this.$route.params.categoryId;
                        axios.get("/getClassifyApps?categoryId=" + categoryId + "&pageSize=60&pageContext=" + pageIndex)
                            .then((res) => {
                                console.dir(res)
                                let appResultList = res.data.apps;
                                for (let i = 0; i < appResultList.length; i++) {
                                    _this.appResultList.push(appResultList[i]);
                                }
                                flag = true;
                            });
                    }
                }
            });
        }
    }
</script>

<style lang="less" scoped>
    * {
        margin: 0;
        padding: 0;
    }

    html, body {
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    ul, li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: #000000;
    }

    img {
        vertical-align: top;
    }

    div.cartoonIndex{
        width: 100%;
    }

    div.cartoonIndex ul{
        clear: both;
    }

    li.common_li {
        float: left;
        margin: 0.3125rem;
        border: 0.0625rem solid #CCCCCC;
        text-align: center;
        cursor: pointer;
        perspective: 50rem;
        perspective-origin: 50% 50%;
    }

    li.common_li p {
        padding: 0.375rem;
        min-width: 3.125rem;
        height: 3.125rem;
        line-height: 3.125rem;
        transition: all 1s;
        background-color: #FFFFFF;
    }

    p.p_class {
        font-weight: bolder;
        color: #000000;
    }
</style>